:root {
  --blockquote-font-size: var(--text-base-size);
  --blockquote-border-size: 4px;
  --blockquote-paddings: var(--spacing-unit);
  --blockquote-margins: var(--spacing-sm) 0;
  --blockquote-background: transparent;
  --blockquote-border-color: var(--color-contrast-low); }

blockquote {
  font-size: var(--blockquote-font-size);
  padding: var(--blockquote-paddings);
  margin: var(--blockquote-margins);
  border-left: var(--blockquote-border-size) solid var(--blockquote-border-color);
  background: var(--blockquote-background); }

blockquote p:last-child {
  margin: unset; }

fieldset {
  min-width: 0;
  padding: unset;
  margin: unset;
  border: unset; }

legend {
  display: block;
  width: 100%;
  padding: unset;
  margin-bottom: var(--spacing-sm); }

:focus {
  outline: none; }

* {
  box-sizing: border-box;
  min-width: 0; }

:root {
  --heading-font-family: Montserrat, sans-serif;
  --heading-line-height: 1.3;
  --heading-color: var(--color-primary); }

[data-theme="dark"] {
  --heading-color: hsl(189, 69%, 58%); }

h1 {
  font-size: var(--text-xxl);
  font-weight: 500; }

h2 {
  font-size: var(--text-xl);
  font-weight: 600; }

h3 {
  font-size: var(--text-lg);
  font-weight: 600; }

h4 {
  font-size: var(--text-md);
  font-weight: 600; }

h5 {
  font-size: var(--text-sm);
  font-weight: 700; }

h6 {
  font-size: var(--text-xs);
  font-weight: 700; }

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: var(--heading-font-family);
  line-height: var(--heading-line-height);
  margin-bottom: var(--spacing-sm);
  color: var(--heading-color);
  word-break: break-word; }

hr {
  margin: var(--spacing-md) 0;
  border: unset;
  border-top: 1px solid var(--color-contrast-lower); }

img {
  max-width: 100%;
  height: auto;
  font-style: italic;
  vertical-align: middle; }

:root {
  --link-color: var(--color-accent);
  --link-color-hover: hsl(var(--color-accent-h), var(--color-accent-s), calc(var(--color-accent-l) - 20%)); }

[data-theme="dark"] {
  --link-color-hover: hsl(var(--color-accent-h), var(--color-accent-s), calc(var(--color-accent-l) + 20%)); }

a {
  text-decoration: none;
  word-break: break-word;
  color: var(--link-color); }
  a:hover {
    text-decoration: underline;
    color: var(--link-color-hover); }

:root {
  --list-margin: 0 0 1rem 0;
  --list-padding: 0 0 0 2rem; }

ol,
ul {
  margin: var(--list-margin);
  padding: var(--list-padding); }

:root {
  --page-background: hsl(var(--color-primary-h), var(--color-primary-s), 98%); }

[data-theme="dark"] {
  --page-background: hsl(252, 11%, 9%); }

body {
  background: var(--page-background); }

:root {
  --selection-background: hsla(var(--color-accent-h), var(--color-accent-s), var(--color-accent-l), 0.2);
  --selection-color: var(--text-color); }

::selection {
  background: var(--selection-background);
  color: var(--selection-color); }

:root {
  --text-base-size: 1em;
  --text-scale-ratio: 1.2;
  --text-xs: 13px;
  --text-sm: 14px;
  --text-md: 20px;
  --text-lg: 24px;
  --text-xl: 28px;
  --text-xxl: 40px;
  --text-color: hsl(0, 0%, 14%);
  --font-primary: 'Roboto', sans-serif;
  --font-secondary: serif;
  --font-monospace: 'Fira Code', monospace;
  --font-family: var(--font-primary);
  --body-line-height: 1.65;
  --mark-background: hsl(60, 100%, 50%);
  --code-background: hsla(210, 12.9%, 12.2%, 0.05); }

[data-theme="dark"] {
  --text-color: hsl(0, 0%, 100%);
  --code-background: hsl(0, 0%, 17%); }

body {
  font-family: var(--font-family);
  font-size: var(--text-base-size);
  line-height: var(--body-line-height);
  color: var(--text-color); }

p {
  margin-top: unset;
  margin-bottom: var(--spacing-sm); }

small {
  font-size: 80%;
  font-weight: normal; }

strong {
  font-weight: 500; }

big {
  font-size: 120%;
  font-weight: normal; }

mark {
  padding: var(--spacing-xs);
  background-color: var(--mark-background); }

code {
  font-family: var(--font-monospace);
  font-size: 90%;
  padding: 4px 6px;
  word-break: break-all;
  border-radius: 4px;
  background-color: var(--code-background); }

pre code {
  padding: unset; }
